@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*{
    font-family: 'poppins';
}
/* nav1 css starts  */
.header{
    background-color: white;
    height: 80px;
}
.header .navMenu1, .navMenu2{
    list-style: none;
    float: right;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 80px;
}
.header .navMenu1 li, .navMenu2 li{
    padding: 10px 20px 0px 20px;
    text-align: center;
}

.header .navMenu1 li a, .navMenu2 li a{
    text-decoration: none;
    color: black;
    font-weight: 400;
}
.header .navMenu1 li a.active, .navMenu2 li a.active{
    text-decoration: none;
    color: blue;
    font-weight: 400;
}
.header .navMenu1 li a i, .navMenu2 li a i{
   font-size: 20px;
}
.header .navMenu2 li a .loginBtn{
    background-color: #900c3d;
    color: white;
    border-radius: 30px;
    padding: 5px 7px;
}
.header .navMenu2 li a .loginBtn i{
    background-color: white;
    color: #900c3d;
    border-radius: 30px;
    padding: 5px 10px;
    font-size: 20px;
}
.header .navMenu2 li a .navMenu3{
    color: #900c3d;
    background-color: white;
    padding: 5px 8px;
    border-radius: 5px;
    border: 2px solid #900c3d;
    font-size: 27px;
}
/* nav1 css  ends */
/* nav2 css starts ends */
nav {
    position: relative;
    border-top: 50px solid #910b3d;
    border-left: 30px solid #d1313100;
    border-right: 30px solid transparent;
    justify-content: center;
    align-items: center;
    z-index: 9999;
 
}
 nav .BottomNav {
    list-style: none;
    display: inline-flex;
    font-size: 18px;
    font-weight: 500;
    position: absolute;
    top: -39px;
}
nav  .BottomNav li a{
    padding: 12px 15px;
    text-decoration: none;
    color: white;
    cursor: pointer;
}
nav .BottomNavmini li a.active {
    text-decoration: none;
    color: blue;
    cursor: pointer;
}
/* nav2 css  ends */


/* responsive css */
@media only screen and (min-width: 1199px) {
    .navMenu2 li:nth-child(2){
        display: none;
    }
    nav .BottomNavmini {
        display: none;
    }
}
@media only screen and (max-width: 1400px) {
    .header .navMenu1 li, .navMenu2 li{
        padding: 0px 10px;
        text-align: center;
    }
}
@media only screen and (max-width: 1199px) {

    .header .navMenu1{
        display: none;
    }
    nav{
        border: none;
    }
    nav .BottomNav {
        display: none;
    }
    nav .BottomNavmini {
        list-style: none;
        display: block;
        font-size: 18px;
        font-weight: 500;
        position: absolute;
        top: 0px;
        left: 0%;
        color: black;
        background-color: #900c3d;
    }
    nav  .BottomNavmini li{
        padding: 12px 15px;
    }
    nav  .BottomNavmini li a{
        text-decoration: none;
        color: white;
        cursor: pointer;
    }

}
.moveTo1{
    margin-top: -52px;
}


/* login page css starts */
.lPage .PLOGIN{
    text-align: center;
}
.lPage .PLOGIN .Logo{
    width: 80%;
}

.lPage .form-floating {
    border-bottom: 1px solid black;
}
.lPage .form-floating:focus {
    outline: none;
  }

.lPage .form-control {
    border: none;
}
.lPage .form-control:focus {
    outline: none;
    box-shadow: none;
  }
  .lPage .SignIn ,.RequestOTP{
    background-color: #900c3d;
    color: white;
    text-decoration: none;
    width: 100%;
    height: auto;
    border-radius: 0;
    font-weight: 500;
    font-size: 18px;
  }
  .lPage .SignIn:hover , .lPage.RequestOTP:hover{
    background-color: #fecb35;
    color:#900c3d;
    font-weight: 600;
  }
 .lPage .RequestOTP:hover{
    background-color: #fecb35;
    color: #900c3d;
    font-weight: 600;
  }
/* login page css ends */







