/* heading with design */
.section-heading {
    text-align: center;
    margin-bottom: 20px;
    font-size: 60px;
}


*{
  padding: 0;
  margin: 0;
}

/* Heading css */
.service-item {
  border-color: rgb(131 227 232 / 80%) !important;
  position: relative;
  height: 385px;
  width: auto;
  border-radius: 8px;
  background: white;
}
.btn-square2 {
  width: 64px;
  height: 64px;
  font-size: 20px;
  color: #097fec;
  margin: auto;
}
.service-item .btn {
  display: inline-flex;
  align-items: center;
  color: #FFFFFF;
  border-radius: 38px;
  position: absolute;
  bottom: 2px;
}
.service-item:hover {
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)!important;
  transition: .2s ease-in-out;
  z-index: 1;
  border: none;
  border-radius: 0;
}
.service-item .btn-square2 i {
  width: 64px;
  height: 64px;
  font-size: 20px;
  color: #097fec;
  display: flex;
  justify-content: center;
  align-items: center;
}




